import { Button, message, Popconfirm } from "antd";
import axios from "axios";

export function Demo3Delete({ id ,onSuccess}: { id: number,onSuccess:()=>void }) {
  async function onConfirm() {
    //删除函数
    try {
      const resp = await axios.delete<string>(`http://localhost:8080/delete/${id}`);
      message.success("删除成功");
      onSuccess && onSuccess()
    } catch (error) {
      console.log(error);
      message.error("删除错误");
    }
  }
  return (
    <Popconfirm title="确定要删除吗？" onConfirm={onConfirm}>
      <Button danger size="small">
        删除
      </Button>
    </Popconfirm>
  );
}
